/* eslint-disable prettier/prettier */
/* global algoliasearch instantsearch */

import injectScript from 'scriptjs';

injectScript(
  'https://maps.googleapis.com/maps/api/js?v=quarterly&key=AIzaSyB5bA37UmmnyvSpvrX3t9mhuUkVTn84Rlg',
  () => {
    const searchClient = algoliasearch(
      'J8TVT53HPZ',
      '981beefd23fe4df6a58e171df0e03aa1'
    );

    const search = instantsearch({
      indexName: 'darcie_services_search',
      searchClient
    });

    search.addWidgets([
      instantsearch.widgets.searchBox({
        container: '#searchbox'
      }),
      instantsearch.widgets.geoSearch({
        container: '#maps',
        googleReference: window.google
      }),
      instantsearch.widgets.hits({
        container: '#hits',
        templates: {
          item: `
    <article>
      <h1>{{#helpers.highlight}}{ "attribute": "objectID" }{{/helpers.highlight}}</h1>
      <p>{{#helpers.highlight}}{ "attribute": "service" }{{/helpers.highlight}}</p>
      <p>{{#helpers.highlight}}{ "attribute": "address" }{{/helpers.highlight}}</p>
      <p>{{#helpers.highlight}}{ "attribute": "hours" }{{/helpers.highlight}}</p>
    </article>
    `
        }
      }),
      // instantsearch.widgets.refinementList({
      //   container: '#brand-list',
      //   attribute: 'brand'
      // }),
      instantsearch.widgets.pagination({
        container: '#pagination'
      })
    ]);

    search.start();
  }
);
